.modal {
  position: fixed;
  color: black;
  display: flex;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: #00000075;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.5s /*cubic-bezier(0.84, 2.1, 1,-0.73)*/;
}

.modal--show {
  opacity: 1;
  pointer-events: unset;
}

.modal-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  height: 40%;
  width: 40%;
  border-radius: 9px;
  background-color: white;
}

.modal-content {
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
  height: 100%;
}

.modal-titulo {
  font-size: 2em;
}

.modal-social-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 9px;
}

.modal-social {
  display: flex;
  align-items: center;
  gap: 15px;
}

.social-links:hover {
  color: blue;
}

.modal-copyicon {
  cursor: pointer;
  transition: all 0.1s;
}

.modal-copyicon:hover {
  color: green;
  transform: scale(1.2);
}
