footer {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  height: 120px;
}

.footer-titulo {
  font-weight: bold;
  font-size: 20px;
  font-family: "Shantell Sans";
}

.footer-buttons {
  display: flex;
  gap: 15px;
}

.footer-boton {
  padding: 10px;
  font-size: 15px;
  display: flex;
  align-items: center;
  border: none;
  background-color: #f0f0f0;
  border-radius: 5px;
  gap: 8px;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}

.footer-boton:hover {
  color: white;
}

.footer-boton:nth-child(1):hover {
  background-color: #f41268;
}

.footer-boton:nth-child(2):hover {
  background-color: #505257;
}

.footer-boton:nth-child(3):hover {
  background-color: #9147ff;
}

@media screen and (max-width: 400px) {
  .footer-boton {
    padding: 13px;
  }

  .footer-boton span {
    display: none;
  }
}