#inicio {
  height: 90vh;
  justify-content: space-between;
}

.inicio-container {
  display: grid;
  width: 20rem;
  gap: 20px;
}

.inicio-logo {
  height: 200px;
}

.inicio-text {
  text-align: start;
}

.inicio-titulo {
  display: flex;
  justify-content: space-between;
  font-size: 50px;
  font-family: "Shantell Sans", sans-serif;
}

.saludar:hover {
  animation: saludo-animacion 0.5s;
  cursor: pointer;
}

.inicio-subtitulo {
  font-size: 30px;
}

.inicio-boton {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  background-color: #2dc4e7;
  padding: 15px;
  font-weight: bold;
  font-size: 20px;
  border: solid black;
  border-radius: 15px;
  transition: background-color 0.5s, color 0.5s;
  cursor: pointer;
}

.inicio-boton:hover {
  background-color: #000000;
  color: white;
}

.inicio-boton:hover span {
  color: white;
}

.span-icono {
  display: flex;
}

.icono-curriculum {
  transition: all 0.5s;
}

.inicio-boton:hover .icono-curriculum {
  transform: translateX(10px);
}

@keyframes saludo-animacion {
  0% {
    transform: rotate(0deg);
  }
  20% {
    transform: rotate(10deg);
  }
  40% {
    transform: rotate(0deg);
  }
  60% {
    transform: rotate(-10deg);
  }
  80% {
    transform: rotate(-5deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@media screen and (max-width: 650px) {
  #inicio {
    flex-direction: column;
    justify-content: space-evenly;
  }
}
